@import url('https://fonts.googleapis.com/css?family=Bangers|Abel');

.App {
  text-align: center;
}

.App-header {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  padding: 10px 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  color: white;
  text-align: left;
  background-image: url("img/bg.jpg");
  background-size: cover;
  background-position: center;
  /* filter: blur(8px);
  -webkit-filter: blur(8px); */
}
.App-title{
  font-size: 3em;
}
.App-description{
  background-color: #111;
  color: #999;
  padding: 2px 0;
  font-size: 0.9em;
}
a{
  text-decoration: none;
  font-weight: bold;
  color: white;
}
.Form{
    margin-top: 20px;
    background-color: #222;
    display: inline-block;
    border-radius: 5px;
    padding: 7px 20px;
    height: 30px;
}

.Button{
  cursor: pointer;
  font-size:120%;
  color:white;
  border-radius: 2px;
  padding:3px 20px;
  border: 0;
  background-color: #111;
  font-family: 'Abel', sans-serif;
}

.Input{
  outline: none;
  width: 400px;
  font-size:100%;
  color: white;
  background-color:#222;
  border:0;
  border-radius: 3px;
  padding: 5px 10px;
  font-family: 'Abel', sans-serif;
}

.qualHeader{
  background-color: #fff;
  color: #000;
  padding: 2px 5px;
  border-radius: 2px;
}
.results-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    text-decoration: underline;
}
.img-container{
  -o-object-fit: cover;
     object-fit: cover;
  width: 400px;
  height: 225px;
  padding: 10px 10px;
}

@media(max-width:1023px) {
  .App-header{
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }
  .Form{
    width: 300px;
  }
  .Input{
    width: 300px;
    font-size: 0.9em;
  }
  .App-description{
    font-size: 0.8em;
  }
  .results-container{
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .img-container{
    width: 350px;
    padding: 10px 5px;
  }
}



